@import url(https://fonts.googleapis.com/css?family=Montserrat|Roboto&subset=latin-ext);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Roboto', sans-serif;
}
.App {
  display: grid;
  grid-template-rows: 10% 1fr 20% 1fr 9px 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas:
    "burger burger burger"
    "header header header"
    "image image image"
    "temp temp temp"
    "line line line"
    "sun wind pressure";
  height: 100vh;
}

.App.day {
  background-color: #FBFBFB;
}

.App.night {
  background-color: #040404;
}

@media(orientation: landscape) {
  .App {
    display: grid;
    grid-template-rows: 1fr 25% 25% 1fr;
    grid-template-areas:
      "header header header"
      "image image image"
      "temp temp temp"
      "sun wind pressure";
  }

  body {
    background-color: #040404;
    font-family: 'Montserrat', sans-serif;
  }

}

@media(min-width: 1024px) and (orientation: landscape) {
  .App {
    grid-template-areas:
      "burger burger burger"
      "header header header"
      "image image image"
      "temp temp temp"
      "line line line"
      "sun wind pressure";
    grid-template-rows: 1% 1fr 30% 1fr 9px 1fr;

  }

}
/*______________________________________GENERAL BULID________________________*/
header.header {
    grid-area: header;
    text-align: center;
}

header.header h1 {
    font-size: 2.5rem;
    margin: 0;
    line-height: 10vh;
    text-transform: uppercase;
    font-weight: medium;
    letter-spacing: 2px;
}

header.header h4 {
    margin: 0;
    font-size: 1.3rem;
    letter-spacing: 2px;
}

/* _________________________________________________________________________ */

/*______________________________________DAY BULID________________________*/
.header.day h1 {
    color: #040404;
}

.header.day h4 {
    color: #5D5D5D;
}

/* _________________________________________________________________________ */


/*______________________________________NIGHT BULID________________________*/

.header.night h1 {
    color: #f5f6fa;
}

.header.night h4 {
    color: #7f8c8d;
}

/* _________________________________________________________________________ */

/* ________________________RESPONSIVE_________________________ */
@media(orientation: landscape) {
    .header.night {
        margin: 3% 0 0 5%;
    }

}


@media(max-width: 350px) {
    header.header h1 {
        font-size: 2rem;
    }


    header.header h4 {

        font-size: 1.2rem;

    }
}

@media(min-width: 1024px) and (orientation: landscape) {

    header.header h1 {
        font-size: 3rem;
        margin-top: 5vh;
    }

}
div.sunrise {
    grid-area: sun;
    text-align: center;
    margin-top: 20px;
}

.sunrise,
.wind,
.pressure {
    background-color: #FBFBFB;
    font-size: .9rem;
}

.sunrise.night,
.wind.night,
.pressure.night {
    background-color: #040404;
}

.sunriseIco {
    color: #f79809;
}

div.wind {
    grid-area: wind;
    text-align: center;
    margin-top: 20px;
}

.windIco {
    color: #0ddce0;
}

div.pressure {
    grid-area: pressure;
    text-align: center;
    margin-top: 20px;
}

.pressureIco {
    color: #ed1e5c
}

div>h2 {
    font-weight: normal;
    line-height: 9vh;
    text-align: center;
}


div>h4 {
    color: #5D5D5D;
}

div.sunsetInfo.night {
    grid-area: pressure;
    text-align: center;
    margin-top: 20px;
}

.moonIco {
    color: #f7d709;
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
}

div.night h2 {
    color: #f5f6fa;
    background-color: #040404;
}

div.night h4 {
    color: #7f8c8d;
}

/* ________________________RESPONSIVE_________________________ */
@media(orientation: landscape) {

    div.sunrise,
    div.wind,
    div.pressure {
        margin-top: -10px;
        overflow: hidden;
    }

    div.sunrise.night,
    div.wind.night,
    div.sunsetInfo.night {
        margin-top: -10px;
    }

}


@media(max-width: 350px) {

    div h2 {
        font-size: 1.3rem;
        line-height: 7vh;
    }


}

@media(min-width: 1024px) and (orientation: landscape) {

    div.sunrise,
    div.wind,
    div.pressure {
        font-size: 1.2rem;
    }

    div.sunrise.night,
    div.wind.night,
    div.sunsetInfo.night {
        font-size: 1.2rem;
    }
}
/* ___________________________GENERAL BULID____________________________ */

div.image {
    grid-area: image;
    position: relative;
}

div.firstCloud {
    position: absolute;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 2;
}

div.secondCloud {
    position: absolute;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

/* ______________________________________________________________________ */

/* ___________________________DAY BULID____________________________ */

.day .cloud {
    color: #00a8ff;
    -webkit-animation: clouds 7s linear infinite alternate;
            animation: clouds 7s linear infinite alternate;
}

.day .firstCloud {
    top: 70%;
    left: 75%;
}

.day .secondCloud {
    top: 15%;
    left: 20%;
}

.image.day>.sun {
    width: 90px;
    height: 90px;
    border-radius: 50%;
    background-image: -webkit-radial-gradient(circle, #f4b642, #f4c23a, #f2cf33, #eedc2d, #e7ea2a);
    background-image: radial-gradient(circle, #f4b642, #f4c23a, #f2cf33, #eedc2d, #e7ea2a);
    position: absolute;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.25), 0 8px 8px rgba(0, 0, 0, 0.22);
}

/* ______________________________________________________________________ */

/* ___________________________NIGHT BULID____________________________ */

div.image.night {
    background: url(data:image/png;base64,);
}

.night>.secondCloud {
    top: 30%;
    left: 75%;
    font-size: 0.9rem;
}

.night>.firstCloud {
    top: 75%;
    left: 21%;
}

.image.night .cloud {
    color: #ecf0f1;
    -webkit-animation: clouds 5s linear infinite alternate;
            animation: clouds 5s linear infinite alternate;
}

.image.night .moon {
    color: #f7d709;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%) rotate(-15deg);
            transform: translate(-50%, -50%) rotate(-15deg);
    -webkit-animation: moon 3s linear infinite alternate;
            animation: moon 3s linear infinite alternate;
}

/* ______________________________________________________________________ */

/* ________________________RESPONSIVE_________________________ */

@media (orientation: landscape) {
    .image.day>.sun {
        width: 80px;
        height: 80px;
        top: 50%;
    }

    .day>.firstCloud {
        top: 40%;
        left: 70%;
    }

    .day>.secondCloud {
        top: 60%;
        left: 30%;
    }

    div.image.night {
        background: none;
    }

    .image.night .moon {
        font-size: 5rem;
    }

    .night>.secondCloud {
        top: 40%;
        left: 70%;
        font-size: 0.9rem;
    }

    .night>.firstCloud {
        top: 60%;
        left: 30%;
    }
}

@media(min-width: 1024px) and (orientation: landscape) {


    div.firstCloud {
        font-size: 1rem;
    }

    div.secondCloud {
        font-size: 2.2rem;
    }

    .image.day>.sun {
        width: 130px;
        height: 130px;
    }

    .image.night .moon {
        font-size: 6.5rem;
    }
}


@-webkit-keyframes clouds {
    from {
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    to {
        -webkit-transform: scale(1.4);
                transform: scale(1.4);
    }
}


@keyframes clouds {
    from {
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    to {
        -webkit-transform: scale(1.4);
                transform: scale(1.4);
    }
}

@-webkit-keyframes moon {
    from {
        -webkit-transform: translate(-50%, -50%) rotate(-15deg);
                transform: translate(-50%, -50%) rotate(-15deg);
    }

    to {
        -webkit-transform: translate(-50%, -50%) rotate(-25deg);
                transform: translate(-50%, -50%) rotate(-25deg);
    }
}

@keyframes moon {
    from {
        -webkit-transform: translate(-50%, -50%) rotate(-15deg);
                transform: translate(-50%, -50%) rotate(-15deg);
    }

    to {
        -webkit-transform: translate(-50%, -50%) rotate(-25deg);
                transform: translate(-50%, -50%) rotate(-25deg);
    }
}
/* _________________________GENERAL BULID________________________________ */
div.temp {
    grid-area: temp;
    text-align: center;
    font-size: 1.5rem;
    margin-bottom: 5vh;
}

div.temp h2 {
    font-size: 1.6rem;
    margin: 0 0 10px 0;
}

div.temp h2::after {
    content: '';
    height: 2px;
    display: block;
    grid-area: line;
    background-color: #95a5a6;
    width: 60%;
    margin: 0 auto;
}

.temp h2>span {
    color: #41a9f4;
    font-weight: bold;
}

/* ______________________________________________________________________ */


/* _________________________DAY BULID________________________________ */
.temp.day {
    color: #040404;
}

.temp.day h2 {
    color: #95a5a6;
}

/* ______________________________________________________________________ */

/* _________________________NIGHT BULID________________________________ */
.temp.night {
    color: #f5f6fa;

}

.temp.night h2 {
    color: #7f8c8d;
}

/* ______________________________________________________________________ */

/* ____________________________RESPONSIVE_________________________________ */
@media(orientation: landscape) {
    .temp h2 {
        display: none;
    }

    .temp h1 {
        font-size: 3rem;
    }


}

@media(orientation: landscape) and (min-width: 1024px) {

    .temp h1 {
        font-size: 3.5rem;
    }

    div.temp h2 {
        display: block;
        font-size: 2rem;


    }

    div.temp h2::after {
        width: 25%;

    }


}

@media(max-width: 350px) {

    div.temp h2::after {
        display: none;
    }

    div.temp {

        font-size: 1.5rem;
    }

}
:root {
    --black: #040404;
    --itemsColor: #FFBF59;
    --whiteTextColor: #f7f6f2;
    --textFont: 'Montserrat', sans-serif;
}

div.burger {
    grid-area: menu;
    width: 50px;
    height: 40px;
    position: absolute;
    top: 5%;
    left: 2%;
    cursor: pointer;
    z-index: 10;
}

div.burger span,
div.burger::before,
div.burger::after {
    display: block;
    position: absolute;
    left: 0;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    height: 5px;
    background-color: #f7f6f2;
    background-color: var(--whiteTextColor);
    width: 50px;
    border-radius: 10px;
    -webkit-transition: all 0.3s cubic-bezier(0.71, 0.02, 0.13, 1.66);
    transition: all 0.3s cubic-bezier(0.71, 0.02, 0.13, 1.66);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.day div.burger span,
.day div.burger::before,
.day div.burger::after {
    background-color: #040404;
    background-color: var(--black);
}

div.burger span {
    top: 50%;
    width: 35px;
}

div.burger::before {
    content: "";
    top: 0;

}

div.burger::after {
    content: "";
    top: 100%;
    width: 45px;
}

.burger.active span,
.burger.active::before,
.burger.active::after {
    background-color: #f7f6f2;
    background-color: var(--whiteTextColor);
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
            transform: translate(-50%, -50%) rotate(45deg);
    width: 100%;
}

.day .burger.active span,
.day .burger.active::before,
.day .burger.active::after {
    background-color: #f7f6f2;
    background-color: var(--whiteTextColor);
}

div.burger.active::after {
    -webkit-transform: translate(-50%, -50%) rotate(-45deg);
            transform: translate(-50%, -50%) rotate(-45deg);
}

div.burger:hover span,
div.burger:hover::after {
    width: 100%;
}

.menu {
    position: absolute;
    list-style-type: none;
    top: 0;
    left: -100%;
    background-color: #040404;
    height: 100%;
    width: 30vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    line-height: 3;
    -webkit-transition: all 0.3s cubic-bezier(1, 0.04, 0, 1);
    transition: all 0.3s cubic-bezier(1, 0.04, 0, 1);
    font-size: 2rem;
    z-index: 9;
    letter-spacing: 3px;
    font-weight: bold;
    overflow: hidden;
}

.menu a {
    color: #f7f6f2;
    color: var(--whiteTextColor);
}

.menu.active {
    left: 0;
}

/* _____________________________________________ */

/* form starting stylings ------------------------------- */
.group {
    position: relative;
    margin-bottom: 15vh;
}

input {
    font-family: 'Montserrat', sans-serif;
    font-family: var(--textFont);
    font-size: 2rem;
    padding: 30px 10px 10px 5px;
    display: block;
    width: 300px;
    border: none;
    border-bottom: 1px solid #757575;
    background-color: #040404;
    color: #f7f6f2;
    color: var(--whiteTextColor);
    text-align: center;
}

input:focus {
    outline: none;
}

/* LABEL ======================================= */
label {
    color: #757575;
    font-size: 1.9rem;
    font-weight: normal;
    position: absolute;
    pointer-events: none;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: 0;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}

/* active state */
input:focus~label,
input:valid~label {
    top: -20px;
    font-size: 14px;
    color: #FFBF59;
    color: var(--itemsColor);
}

/* BOTTOM BARS ================================= */
.bar {
    position: relative;
    display: block;
    width: 300px;

}

.bar:before,
.bar:after {
    content: '';
    height: 3px;
    width: 0;
    bottom: -1px;
    position: absolute;
    background: #FFBF59;
    background: var(--itemsColor);
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}

.bar:before {
    left: 50%;
}

.bar:after {
    right: 50%;
}

/* active state */
input:focus~.bar:before,
input:focus~.bar:after {
    width: 50%;
}

/* HIGHLIGHTER ================================== */
.highlight {
    position: absolute;
    width: 100px;
    top: 25%;
    left: 0;
    pointer-events: none;
    opacity: 0.5;
}

/* active state */
input:focus~.highlight {
    -webkit-animation: inputHighlighter 0.3s ease;
    animation: inputHighlighter 0.3s ease;
}



button.applay {
    font-size: 1.5rem;
    padding: 10px 25px;
    background-color: transparent;
    color: #f7f6f2;
    color: var(--whiteTextColor);
    border: 3px solid #FFBF59;
    border: 3px solid var(--itemsColor);
    font-family: 'Montserrat', sans-serif;
    font-family: var(--textFont);
    letter-spacing: 2px;
    border-radius: 5px;
    font-weight: bold;
    -webkit-transition: border-color, -webkit-transform 0.2s ease-out;
    transition: border-color, -webkit-transform 0.2s ease-out;
    transition: border-color, transform 0.2s ease-out;
    transition: border-color, transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
}

button.applay.shake {
    -webkit-animation: shake .5s ease-out;
            animation: shake .5s ease-out;
    border-color: #db1547;
}

div.Contact {
    width: 20vw;
    height: 12vh;
    position: absolute;
    bottom: 0;
    right: -3%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    display: flex;
    justify-content: space-between;
    font-size: 2.5rem;
}

/* _____________________________________________ */
@media (orientation: portrait) {
    div.burger {
        top: 4%;
        left: 5%;
        width: 35px;
        height: 25px;
    }

    .menu {
        top: -100vh;
        left: 0;
        width: 100%;
        line-height: 3;
        -webkit-transition: all 0.3s cubic-bezier(1, 0.04, 0, 1);
        transition: all 0.3s cubic-bezier(1, 0.04, 0, 1);
        font-size: 1.5rem;
    }

    .menu.active {
        top: 0;
    }

    div.burger span,
    div.burger::after,
    div.burger::before {
        width: 100%;
        height: 4px;
    }


    .bar:before,
    .bar:after {

        height: 2px;

    }

    div.Contact {
        width: 50vw;
        bottom: 0;
        right: 0;
        font-size: 2rem;
    }

}



@media (max-width: 320px) and (orientation: portrait) {
    .menu {
        font-size: 1.2rem;
    }

    input {

        width: 220px;
        margin: 0 auto;

    }

    label {
        font-size: 1.6rem;
    }

    button.applay {
        font-size: 1.1rem;

    }
}

@media (max-width: 1024px) and (orientation: landscape) {
    div.burger {
        top: 5%;
        left: 3%;
        width: 30px;
        height: 20px;
    }

    .menu {
        top: 0;
        left: -100%;
        min-width: 100%;
        line-height: 2.5;
        font-size: 2.4rem;
    }

    div.burger span,
    div.burger::after,
    div.burger::before {
        width: 100%;
        height: 3px;
    }

}

@media (max-width: 1500px) and (orientation: landscape) {
    .menu {
        font-size: 1.7rem;
    }

}

@media (min-width: 1024px) and (orientation: landscape) {

    .menu {
        width: 100vw;
    }

    input {

        width: 550px;

    }

    label {
        top: -45%;
        font-size: 2.5rem;

    }

    .bar {

        width: 550px;

    }

    button.applay {
        font-size: 1.5rem;
        padding: 15px 40px;
        cursor: pointer;
    }

    input:focus~label,
    input:valid~label {

        font-size: 18px;

    }

    .Contact .fb svg,
    .in svg,
    .gthub svg,
    .cdpen svg {
        -webkit-transition: all 0.5s cubic-bezier(.99, 0, .57, .94);
        transition: all 0.5s cubic-bezier(.99, 0, .57, .94);
    }

    .Contact .fb svg:hover,
    .in svg:hover,
    .gthub svg:hover,
    .cdpen svg:hover {
        -webkit-transform: rotate(360deg) scale(1.4);
                transform: rotate(360deg) scale(1.4);
        color: #FFBF59;
        color: var(--itemsColor)
    }

    div.Contact a {
        font-size: 2.5rem;
    }



    button.applay:hover {
        -webkit-transform: scale(0.9);
                transform: scale(0.9);
    }

}

@media(orientation: landscape) {
    input {

        width: 550px;

    }

    label {
        top: -45%;
        font-size: 2rem;
    }

    .bar {

        width: 550px;

    }


    input:focus~label,
    input:valid~label {

        font-size: 15px;

    }


    button.applay {
        font-size: 1.1rem;
        padding: 12px 30px;
        cursor: pointer;
    }

    div.Contact {
        width: 50vw;
        bottom: 0%;
        right: 0;
        font-size: 1.5rem;
    }

}

/* ANIMATIONS ================ */
@-webkit-keyframes inputHighlighter {
    from {
        background: #FFBF59;
        background: var(--itemsColor);
    }

    to {
        width: 0;
        background: transparent;
    }
}

@keyframes inputHighlighter {
    from {
        background: #FFBF59;
        background: var(--itemsColor)
    }

    to {
        width: 0;
        background: transparent;
    }
}

@-webkit-keyframes shake {

    from,
    to {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }

    10%,
    30%,
    50%,
    70%,
    90% {
        -webkit-transform: translate3d(-7px, 0, 0);
        transform: translate3d(-10px, 0, 0);
    }

    20%,
    40%,
    60%,
    80% {
        -webkit-transform: translate3d(7px, 0, 0);
        transform: translate3d(10px, 0, 0);
    }

}

@keyframes shake {

    from,
    to {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }

    10%,
    30%,
    50%,
    70%,
    90% {
        -webkit-transform: translate3d(-7px, 0, 0);
        transform: translate3d(-10px, 0, 0);
    }

    20%,
    40%,
    60%,
    80% {
        -webkit-transform: translate3d(7px, 0, 0);
        transform: translate3d(10px, 0, 0);
    }

}
